<style>@font-face {
  font-family: 'majormono';
  src: url("../public/MajorMonoDisplay-Regular.ttf") format("truetype"); }

@font-face {
  font-family: 'xanh';
  src: url("../public/XanhMono-Regular.ttf") format("truetype"); }

.pauseButton {
  background: #006A4E;
  padding: 4px;
  border-radius: 8px;
  margin: 8px 0;
  border: 1px solid white;
  color: white;
  cursor: pointer;
  user-select: none; }

.paused {
  background: #D21034; }
</style>

<script>
	import classNames from 'classnames';
	import { pauseStatus, startupTimer, startupTime } from '../stores';

	const pause = () => {
		pauseStatus.update(paused => !paused);
	};

	let text;

	$: {
		text = $pauseStatus ? 'Start reactor' : 'Power down';

		if (!$pauseStatus && $startupTimer < $startupTime) {
			text = 'Starting...';
		}
	}
</script>

<section class={classNames('pauseButton', { paused: $pauseStatus })} on:click={pause} >
	<h1>{text}</h1>
</section>
